* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  font-family: "AntennaExtraCond";
  line-height: 1;
  color: var(--black);
  background-color: transparent;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

a {
  text-decoration: none;
  color: inherit;
}

ul,
ol {
  list-style: none;
}

button {
  border: none;
  background: none;
  cursor: pointer;
  font-family: inherit;
}

input,
textarea,
select {
  font-family: inherit;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
  font-size: 16px;
}

body {
  font-family: "Antenna", Arial, sans-serif;
  font-weight: 400;
  line-height: 1.4;
  background: transparent;
  color: var(--text);
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1 {
  font-size: 3rem;
  font-weight: 700;
  line-height: 1.4;
  margin: 0;
}

h2 {
  font-size: 2.625rem;
  font-weight: 500;
  line-height: 1.4;
  margin: 0;
}

h3 {
  font-size: 2.25rem;
  font-weight: 600;
  line-height: 1.4;
  margin: 0;
}

h4 {
  font-size: 1.5rem;
  font-weight: 700;
  line-height: 1.4;
  margin: 0;
}

h5 {
  font-size: 1.375rem;
  font-weight: 500;
  line-height: 1.4;
  margin: 0;
}

h6 {
  font-size: 1.25rem;
  font-weight: 700;
  line-height: 1.4;
  margin: 0;
}

p {
  font-size: 0.875rem;
  font-weight: 400;
  line-height: 1.4;
  margin: 0;
}

main.main {
  position: relative;
  width: 100%;
  min-height: 100vh;
}
@media (max-width: 991px) {
  main.main {
    min-height: auto;
  }
}

.container-xl {
  max-width: 1140px;
  margin: 0 auto;
  padding: 0 15px;
}
@media (max-width: 991px) {
  .container-xl {
    padding: 0 16px;
  }
}

.topbar {
  background: var(--white);
  padding: 18px 0;
  position: relative;
  z-index: 1020;
}
@media (max-width: 991px) {
  .topbar {
    padding: 12px 0;
  }
}
.topbar__container {
  padding-left: 80px;
  padding-right: 80px;
}
@media (max-width: 991px) {
  .topbar__container {
    padding-left: 48px;
    padding-right: 48px;
  }
}
@media (max-width: 767px) {
  .topbar__container {
    padding-left: 24px;
    padding-right: 24px;
  }
}
.topbar__content {
  gap: 99px;
  justify-content: space-between;
  position: relative;
}
@media (max-width: 991px) {
  .topbar__content {
    gap: 24px;
  }
}
@media (max-width: 767px) {
  .topbar__content {
    gap: 0;
    justify-content: space-between;
  }
}
.topbar__toggle {
  background: transparent;
  border: none;
  padding: 8px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: 0.3s ease;
  border-radius: 4px;
  flex-shrink: 0;
}
@media (min-width: 992px) {
  .topbar__toggle {
    display: none;
  }
}
.topbar__toggle:hover, .topbar__toggle:focus {
  background: rgb(255, 255, 255);
  outline: none;
}
.topbar__toggle:focus-visible {
  outline: 2px solid var(--primary);
  outline-offset: 2px;
}
.topbar__toggle-icon {
  font-size: 30px;
  color: var(--primary);
  transition: transform 0.3s ease, opacity 0.3s ease;
  transform: rotate(0deg);
  opacity: 1;
  display: block;
}
.topbar__toggle[aria-expanded=true] .topbar__toggle-icon {
  transform: rotate(90deg);
  opacity: 0;
}
.topbar__logo {
  flex-shrink: 0;
  width: 98px;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (max-width: 767px) {
  .topbar__logo {
    flex: 1;
    justify-content: center;
    width: auto;
  }
}
.topbar__logo img {
  display: block;
  height: 72px;
  width: 98px;
}
@media (max-width: 991px) {
  .topbar__logo img {
    height: 56px;
    width: auto;
  }
}
@media (max-width: 767px) {
  .topbar__logo img {
    height: 48px;
    width: auto;
  }
}
.topbar__center {
  max-width: 486px;
  flex: 1;
}
@media (max-width: 991px) {
  .topbar__center {
    order: 3;
    width: 100%;
    max-width: 100%;
    text-align: center;
  }
}
.topbar__title {
  font-family: var(--font-family);
  font-size: var(--font-size-body2);
  font-weight: var(--font-weight-bold);
  line-height: var(--line-height-normal);
  color: var(--gray-1);
  margin: 0;
}
@media (max-width: 767px) {
  .topbar__title {
    font-size: var(--font-size-body3);
  }
}
.topbar__right {
  gap: 36px;
  flex-shrink: 0;
}
@media (max-width: 991px) {
  .topbar__right {
    gap: 24px;
  }
}
@media (max-width: 767px) {
  .topbar__right {
    gap: 12px;
    flex-shrink: 0;
  }
}
.topbar__info {
  gap: 16px;
}
@media (max-width: 767px) {
  .topbar__info {
    width: 100%;
    flex-wrap: wrap;
    gap: 12px;
